@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@400;700&family=Roboto:wght@400;700&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.section {
  max-width: 1440px;
  margin: 0 auto;
}

.main {
  font-family: Roboto, sans-serif;
  display: flex;
  flex-direction: column;
}

@media (min-width: 720px) {
  .main {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
}

@media (min-width: 1024px) {
  .main {
    margin: 50px;
  }
}

@media (min-width: 1220px) {
  .main {
    margin: 70px;
  }
}

.main .img480, .main .img720, .main .img1002, .main .img1400 {
  display: none;
}

.main .sectionImg {
  width: 100%;
}

.main .sectionImg img {
  width: 100%;
}

@media (min-width: 720px) {
  .main .sectionImg {
    width: 50%;
    overflow: hidden;
    display: flex;
    margin-right: 30px;
  }
  .main .sectionImg img {
    width: 80%;
    align-items: center;
  }
}

@media (min-width: 720px) {
  .main .sectionImg .img200, .main .sectionImg .img480, .main .sectionImg .img1002, .main .sectionImg .img1400 {
    display: none;
  }
  .main .sectionImg .img720 {
    display: block;
    width: 100%;
    margin: 40px;
  }
}

@media (min-width: 1024px) {
  .main .sectionImg .img720 {
    display: none;
  }
  .main .sectionImg .img1002 {
    display: block;
    width: 100%;
    margin: 40px;
  }
}

@media (min-width: 720px) {
  .main .sectionContent {
    flex: 1;
  }
}

.main .contentTitle {
  text-align: center;
  margin: 30px;
  font-weight: 700;
}

@media (min-width: 720px) {
  .main .contentTitle {
    font-size: 26px;
    margin-top: 75px;
    text-align: left;
  }
}

@media (min-width: 1024px) {
  .main .contentTitle {
    font-size: 36px;
  }
}

@media (min-width: 1220px) {
  .main .contentTitle {
    font-size: 42px;
  }
}

.main .contentText {
  text-align: center;
  font-size: 20px;
  margin: 0 20px;
}

@media (min-width: 720px) {
  .main .contentText {
    text-align: left;
    font-size: 14px;
    margin: 30px;
  }
}

@media (min-width: 1024px) {
  .main .contentText {
    font-size: 22px;
  }
}

@media (min-width: 1220px) {
  .main .contentText {
    font-size: 24px;
  }
}

.sectionContentContainer {
  display: flex;
  flex-direction: column;
}

.mainBtn {
  display: flex;
  justify-content: center;
}

@media (min-width: 720px) {
  .mainBtn {
    padding: 0;
    font-size: 14px;
    margin-bottom: 20px;
    justify-content: flex-start;
  }
}

#myBtn {
  width: 160px;
  height: 60px;
  /* Button Text */
  font-family: Roboto, sans-serif;
  font-style: normal;
  text-align: center;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  background: #e4c1ae;
  color: #FFFFFF;
  margin: 30px;
  margin-bottom: 50px;
  border-radius: 32px;
  border: none;
}

@media (min-width: 720px) {
  #myBtn {
    width: 120px;
    height: 40px;
  }
}

.closeBtn {
  font-family: Roboto, sans-serif;
  font-style: normal;
  text-align: center;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  background: #e4c1ae;
  color: #FFFFFF;
  padding: 10px;
  margin-bottom: 50px;
  border-radius: 32px;
  border: none;
}

#myBtn {
  padding: 10px 15px;
}

/* The Modal (background) */
.modal {
  display: none;
  /* Hidden by default */
  position: fixed;
  /* Stay in place */
  z-index: 1;
  /* Sit on top */
  left: 0;
  top: 0;
  width: 100%;
  /* Full width */
  height: 100%;
  /* Full height */
  overflow: auto;
  /* Enable scroll if needed */
  background-color: black;
  /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4);
  /* Black w/ opacity */
}

/* Modal Content/Box */
.modal-content {
  position: relative;
  background-color: #fefefe;
  margin: 15% auto;
  /* 15% from the top and centered */
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
  /* Could be more or less, depending on screen size */
}

/* The Close Button */
.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.image {
  height: 200px;
  width: 200px;
  display: block;
}

