/* Modal Content/Box */
.modal-content-alert {
  position: relative;
  background-color: #fefefe;
  margin: 15% auto;
  /* 15% from the top and centered */
  padding: 20px;
  border: 1px solid #888;
}

.alertText {
  font-weight: 400;
  display: block;
  margin: 0 20px 20px;
  font-size: 16px;
}

